import React from 'react';
import { Panel, Group, Header, SimpleCell, InfoRow } from '@vkontakte/vkui';
import { lastStudentsScheduleUpdate, lastTeachersScheduleUpdate } from '../Database';

const Information = props => {
    return (
        <Panel id={props.id}>
            <Group header={<Header mode="secondary"> </Header>}>
                <SimpleCell multiline disabled={true}>
                    <InfoRow header=" ">1.1.1</InfoRow>
                </SimpleCell>
                <SimpleCell multiline disabled={true}>
                    <InfoRow header="   ">{lastStudentsScheduleUpdate()}</InfoRow>
                </SimpleCell>
                <SimpleCell multiline disabled={true}>
                    <InfoRow header="   ">{lastTeachersScheduleUpdate()}</InfoRow>
                </SimpleCell>
            </Group>
        </Panel>
    );
}

export default Information;